<template>
    <div>
        <form>
            <div class='mb-3'>
                <input type='text' class='form-control' v-model='todo.header'>
                <textarea type='textarea' class='form-control mt-3' v-model='todo.text'></textarea>
                <button type="button" class="btn btn-success mt-3" v-on:click='send'> Сохранить </button>
            </div>
        </form>
    </div>
</template>

<script>
export default {
    data() {
        return {
            todo:{
                header: 'New Header',
                text: 'New Text'
            }
        }
    },
    methods: {
        send() {
            this.$emit('send', this.todo)
        }
    }
}
</script>