<template>
    <div class='container'>
        <h1> ToDo-List</h1>
        <div class='row'>
            <div class="col">
                <Input v-on:send='pushTodo'/>
                    <div class='form'>
                        <div class="form-chek">
                            <input type='radio' class="form-check-input" id='active' value='active' v-model='sort' v-on:click='change'/>
                            <label for='active' class="form-check-label ms-3"> Активные </label>
                        </div>
                        <div class="form-chek">
                            <input type='radio' class="form-check-input" id='done' value='done' v-model='sort' v-on:click='change'/>
                            <label for='done' class="form-check-label ms-3"> Завершенные </label>
                        </div>
                        <div class="form-chek">
                            <input type='radio' class="form-check-input" id='all' value='all' v-model='sort' v-on:click='change'/>
                            <label for='all' class="form-check-label ms-3"> Все </label>
                        </div>
                    </div>
            </div>
            <div class="col">
                <div class='card bg-light mb-2' v-for="todo in showTodos" v-bind:key="todo.id">
                    <div class='row'>
                        <div class="col-10">
                            <div>
                                <h5>{{todo.header}}</h5> 
                                <p>{{todo.text}}</p>
                            </div>
                        </div>
                        <div class="col">
                            <input type='checkbox' class="form-check-input mt-2" v-model='todo.state'/>
                            <button type="button" class="btn btn-danger mt-1 btn-sm ms-3" v-on:click="remove(todo.id)"> x </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import Input from './Input.vue'

export default {
    components: {
        Input
    },
    data: function() {
        return {
            todos: [],
            showTodos: [],
            counter: 0,
            sort: 'all'
        }
    },
    methods: {
        pushTodo(data){
            this.todos.push({header:data.header, text:data.text, id:this.counter++, state:false})
            this.showTodos = this.todos
        },
        remove(data){
            this.todos = this.todos.filter(todo => {return todo.id != data})
            this.showTodos = this.todos
        },
        checkState(){
                 if (this.sort == 'active') {
                this.showTodos = this.todos.filter(todo => {return todo.state == false})
                } else if (this.sort == 'done') {
                    this.showTodos = this.todos.filter(todo => {return todo.state == true})
                } else {
                    this.showTodos = this.todos
                }
            }, 
        change() {
            setTimeout(this.checkState, 10)
        },
    },
}
</script>
